  appearance: none;
  display: block;
  position: relative;
  z-index: 1;
  cursor: pointer; }
  .btn-v2::before {
    z-index: 2;
    content: '';
    display: block;
    position: absolute;
    left: 0;
    bottom: -5px;
    height: 50%;
    width: 100%;
    background-color: #6db44a;
    border-radius: 20px;
    transition-duration: .2s;
    box-shadow: -1px 8px 24px -4px rgba(0, 0, 0, 0.21); }
  .btn-v2 .btn-v2__inner {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    z-index: 3;
    min-height: 83px;
    top: 0;
    transition-duration: .2s; }
  .btn-v2__bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    border-radius: 10px;
    background-color: #54af3e;
    background-image: linear-gradient(54deg, rgba(84, 175, 62, 0.97) 0%, rgba(144, 236, 47, 0.97) 100%);
    background-size: 200% 600%;
    background-repeat: no-repeat;
    background-position: 0% 100%;
    transition-duration: .5s;
    animation: btn-v2-bg 5s linear infinite; }
    .btn-v2__bg::before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 50%;
      height: 100%;
      background: -moz-linear-gradient(336deg, rgba(255, 255, 255, 0.21) 0%, rgba(255, 255, 255, 0) 75%, rgba(255, 255, 255, 0) 100%);
      /* ff3.6+ */
      background: -webkit-gradient(linear, left top, right bottom, color-stop(0%, rgba(255, 255, 255, 0.21)), color-stop(75%, rgba(255, 255, 255, 0)), color-stop(100%, rgba(255, 255, 255, 0)));
      /* safari4+,chrome */
      background: -webkit-linear-gradient(336deg, rgba(255, 255, 255, 0.21) 0%, rgba(255, 255, 255, 0) 75%, rgba(255, 255, 255, 0) 100%);
      /* safari5.1+,chrome10+ */
      background: -o-linear-gradient(336deg, rgba(255, 255, 255, 0.21) 0%, rgba(255, 255, 255, 0) 75%, rgba(255, 255, 255, 0) 100%);
      /* opera 11.10+ */
      background: -ms-linear-gradient(336deg, rgba(255, 255, 255, 0.21) 0%, rgba(255, 255, 255, 0) 75%, rgba(255, 255, 255, 0) 100%);
      /* ie10+ */
      background: linear-gradient(114deg, rgba(255, 255, 255, 0.21) 0%, rgba(255, 255, 255, 0) 75%, rgba(255, 255, 255, 0) 100%);
      /* w3c */
      filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#FFFFFF', endColorstr='#FFFFFF', GradientType=1);
      /* ie6-9 */
      z-index: 3; }
  .btn-v2__text {
    display: block;
    position: relative;
    z-index: 4;
    text-shadow: 0 2px 0 rgba(110, 185, 71, 0.35);
    color: #ffffff;
    font-size: 14px;
    font-weight: 500;
    line-height: 25.23px;
    text-transform: uppercase;
    letter-spacing: 1.07px;
    border-radius: 10px;
    transition-duration: .2s; }
  .btn-v2:hover .btn-v2__inner {
